.profile .alias{padding:10px;text-align:center;position:relative;-ms-flex:1 0 200px;flex:1 0 200px}.profile .alias:after{width:16px;height:16px;border-radius:50%;border:2px solid #e4e5e7;background:#0c0d0d;z-index:1}.profile .alias:after,.profile .alias:before{content:"";position:absolute;left:50%;top:40px;transform:translate(-50%,-50%)}.profile .alias:before{width:100%;height:2px;background:#e4e5e7}.profile .alias:first-child:before{width:50%;transform:translateY(-50%)}.profile .alias:first-child:after{background:#007bff}.profile .alias:last-child:before{background:linear-gradient(90deg,#e4e5e7,#e4e5e7 50%,hsla(0,0%,100%,0) 80%)}.profile .alias-name{font-weight:700;font-size:1.5em;padding-top:10px;margin-top:15px}